@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fjalla+One:ital,wght@0,400;1,400&display=swap);
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: 'Poppins', sans-serif;
}

:root {
	--primary-color: #080808;
	--secondary-color: #fc4444;
	--accent-color: #ffffff;
	--hover-color: #292929;
	--bg-color: #453b36;
	--text-color: #fc4444;
	--black-gradient: repeating-linear-gradient(
			135deg,
			rgba(145, 145, 145, 0.02) 0px,
			rgba(145, 145, 145, 0.02) 39px,
			rgba(140, 140, 140, 0.02) 39px,
			rgba(140, 140, 140, 0.02) 57px,
			rgba(9, 9, 9, 0.02) 57px,
			rgba(9, 9, 9, 0.02) 91px,
			rgba(217, 217, 217, 0.02) 91px,
			rgba(217, 217, 217, 0.02) 119px,
			rgba(45, 45, 45, 0.02) 119px,
			rgba(45, 45, 45, 0.02) 141px,
			rgba(227, 227, 227, 0.02) 141px,
			rgba(227, 227, 227, 0.02) 184px,
			rgba(236, 236, 236, 0.02) 184px,
			rgba(236, 236, 236, 0.02) 227px,
			rgba(124, 124, 124, 0.02) 227px,
			rgba(124, 124, 124, 0.02) 244px
		),
		repeating-linear-gradient(
			135deg,
			rgba(39, 39, 39, 0.02) 0px,
			rgba(39, 39, 39, 0.02) 23px,
			rgba(2, 2, 2, 0.02) 23px,
			rgba(2, 2, 2, 0.02) 55px,
			rgba(13, 13, 13, 0.02) 55px,
			rgba(13, 13, 13, 0.02) 71px,
			rgba(44, 44, 44, 0.02) 71px,
			rgba(44, 44, 44, 0.02) 98px,
			rgba(240, 240, 240, 0.02) 98px,
			rgba(240, 240, 240, 0.02) 134px,
			rgba(182, 182, 182, 0.02) 134px,
			rgba(182, 182, 182, 0.02) 159px,
			rgba(246, 246, 246, 0.02) 159px,
			rgba(246, 246, 246, 0.02) 174px,
			rgba(157, 157, 157, 0.02) 174px,
			rgba(157, 157, 157, 0.02) 190px
		),
		repeating-linear-gradient(
			135deg,
			rgba(145, 145, 145, 0.02) 0px,
			rgba(145, 145, 145, 0.02) 35px,
			rgba(217, 217, 217, 0.02) 35px,
			rgba(217, 217, 217, 0.02) 53px,
			rgba(199, 199, 199, 0.02) 53px,
			rgba(199, 199, 199, 0.02) 70px,
			rgba(196, 196, 196, 0.02) 70px,
			rgba(196, 196, 196, 0.02) 114px,
			rgba(137, 137, 137, 0.02) 114px,
			rgba(137, 137, 137, 0.02) 143px,
			rgba(249, 249, 249, 0.02) 143px,
			rgba(249, 249, 249, 0.02) 191px,
			rgba(247, 247, 247, 0.02) 191px,
			rgba(247, 247, 247, 0.02) 239px,
			rgba(38, 38, 38, 0.02) 239px,
			rgba(38, 38, 38, 0.02) 273px
		),
		linear-gradient(90deg, rgb(69, 69, 69), rgb(3, 3, 3));

	--black-gradient2: linear-gradient(to right, #000000, #434343);
}

body {
	background: #453b36;
	background: var(--bg-color);
}

.container-main {
    /*border: 5px red solid;*/
    display: flex;
    background: black;
}

/*Anschrift+Kontakt-Container*/
.container-1 {
    /* border: 5px #ccc solid;*/
    padding: 5px 20px;
    width: 60%;
    font-style: 'Poppins', sans-serif;
    color: var(--secondary-color);
    display: flex;
    justify-content: space-between;
    grid-gap: 20px;
    gap: 20px;
    z-index: 1000;
}

/*Copyright-Container*/
.container-2 {
    width: 100%;
    font-style: 'Poppins', sans-serif;
    color: var(--secondary-color);
    margin: auto;
}

/*Logos Container*/
.container-3 {
    /*border: 5px yellow solid;*/
    padding: 5px 20px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 50%;
    grid-gap: 10px;
    gap: 10px;
    z-index: 1000;
}

/*Kontakt*/
.box-1 {
    /*background: dimgray;*/
    list-style-type: none;
    font-size: 13px;
}

/*Anschrift*/
.box-2 {
    /* background: darkslateblue;*/
    list-style-type: none;
    font-size: 13px;
}

/*Copyright zeug ganz unten*/
.box-3 {
    border-top: 1px solid grey;
    background: black;
    font-size: 11px;
    list-style-type: none;
    text-align: center;
    padding: 3px;
}

/*Logos*/
/*.box-4 {
    /*background: black;}*/

a {
    color: var(--secondary-color);
}

@media screen and (max-width: 991px) {
    .container-1 {
        flex-direction: column;
        width: 50%;
    }
    .container-3 {
        width: 50%;
        margin: auto;
    }
}

