	position: relative;
	height: auto;
	top: 8px;
	z-index: -1;
}

.Container-Bild {
	border: 3px solid var(--secondary-color);
	width: 40%;
	height: 100%;
	margin: auto;
	display: flex;
	border-radius: 20px;
	overflow: hidden;
}

